@import url('https://fonts.googleapis.com/css?family=Roboto');

$base-font: 'Roboto', sans-serif;
$red_color: #d83434;

@mixin flex() {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

body {
  font-family: $base-font;
  border-radius: 5px;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn {
  display: inline-block;
  padding: 10px 20px;
  margin: 5px 10px;
  background: $red_color;
  border: none;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  transition: background 0.5s;

  &:hover {
    background: #1c2026;
  }
}

#swap {
  display: inline-block;
  padding: 10px 20px;
  margin: 0 10px;
  border: none;
  background-image: url('../images/swap.png');
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transition: background 0.5s;
  outline: none;

  &:hover {
    opacity: 0.7;
  }
}

.red {
  color: $red_color;
}

header {
  background: #F6F7FB;
  padding: 20px 0;
  box-shadow: 0 4px 6px 0 rgba(10, 10, 10, 0.1);

  .header {
    @include flex();
    justify-content: center;

    .title {
      font-size: 24px;
      font-weight: bold;
      text-transform: uppercase;
    }
  }
}

main {
  min-height: calc(100vh - 161px);
  margin-top: 20px;
  padding: 20px 0;

  .btns {
    text-align: center;
  }

  .form_box {
    display: flex;
    flex-direction: column;
    max-width: 515px;
    margin: 0 auto;

    .line {
      width: 100%;
      @include flex();
      margin: 10px 0;
    }

    @media screen and (max-width: 550px) {
      .line_conversion {
        flex-direction: column;

        select {
          width: 100%;
          max-width: 300px;
        }

        #swap {
          margin: 15px;
        }
      }
    }

    .line_center {
      justify-content: center;
    }

    input, select {
      width: 230px;
      height: 40px;
      padding: 10px;
      border: 1px solid #000;
      border-radius: 7px;
      line-height: 40px;
      outline: none;
    }

    @media screen and (max-width: 550px) {
      input {
        width: 50%;
      }
    }

  }
}

footer {
  padding: 20px 0;
  border-top: 1px solid #000;
  background: #1c2026;
  color: #fff;

  .footer {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
